<template>
<div class="notification-item">
    <div class="item-header d-flex justify-content-between">
        <div class="noti-time">{{notiTime}}</div>
        <div class="noti-close" @click="deleteItem">
            <img :src="closeIcon" alt="">
        </div>
    </div>
    <div class="item-title">
        {{notification.title}}
    </div>
    <div class="item-title">
        {{notification.description}}
    </div>
    <div class="item-detail">
        <button @click="viewDetail">{{$t('Notification.Detail')}}</button>
    </div>
</div>
</template>

<script>
import moment from 'moment'
import closeIcon from '@/assets/images/close-btn.png'

export default {
    name: 'NotificationItem',
    components: {},
    data: function () {
        return {
            notiTime: null,
            closeIcon,
        }
    },
    props: {
        notification: {
            type: Object,
            default: () => ({
                id: 1,
                time: '2019.05.13 13:32',
                title: 'New Project - S Company',
                description: 'SAP FI Consultant, ABAP Developer'
            })
        },
        deleteItem: {
            type: Function,
            default: () => {}
        },
    },
    async created() {
        this.notiTime = moment(this.notification.time).format("YYYY.MM.DD HH:mm");
    },
    methods: {
        viewDetail() {
            this.$router.push({
                name: 'NotificationDetail',
                params: {
                    id: this.notification.id
                }
            })
        }
    }
}
</script>

<style lang="scss">
.notification-item {
    background-color: #ffffff;
    background: #ffffff;
    padding: 1.5rem;
    margin: 1rem 0;

    &:last-child {
        margin-bottom: 0;
    }

    .noti-time {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: 300;
        font-size: 1.5rem;
        word-spacing: 1rem;
    }

    .item-title {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: normal;
        font-size: 1.6rem;
        line-height: 2.5rem;
        padding-top: 1rem;
    }

    .item-detail {
        padding-top: 1rem;

        button {
            height: 3rem;
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 1.3rem;
            line-height: 1.9rem;
            align-items: center;
            color: #006fe3;
            background: #fff;
            border: .1rem solid #006fe3;
            box-sizing: border-box;
            border-radius: 0.4rem;

            &:focus {
                outline: none;
            }
        }
    }
}
</style>
